import React from 'react'
import {Link, graphql, StaticQuery} from 'gatsby'
import SearchBox from '../SearchBox'

const NavBar = ({toggleNavbar, isActive}) => (
  <StaticQuery
    query={graphql`
            query SearchIndexQuery {
                siteSearchIndex {
                    index
                }
            }
        `}
    render={data => (
      <nav className='navbar is-fixed-top' aria-label='main navigation'>
        <div className='navbar-brand'>
          <Link to='/' className='navbar-item'>
            <img src="https://rdbox-intec.github.io/homepage_en/icons/icon-512x512.png"></img>
            <strong>&nbsp;&nbsp;&nbsp;RDBOX</strong>
          </Link>
          <button
            className={`button navbar-burger ${isActive ? 'is-active' : ''}`}
            data-target='navMenu'
            onClick={toggleNavbar}
          >
            <span />
            <span />
            <span />
          </button>
        </div>
        <div className={`navbar-menu ${isActive ? 'is-active' : ''}`} id='navMenu'>

          <div className='navbar-end'>
          <a href="https://rdbox-intec.github.io/homepage_jp/" className="navbar-item">日本語</a>
            <SearchBox searchIndex={data.siteSearchIndex.index} />
            <a href="https://github.com/rdbox-intec/rdbox" className="navbar-item"><strong>GitHub</strong></a>
            <Link className='navbar-item' to='/about'>
                            About
            </Link>
            <Link className='navbar-item' to='/blog'>
                            Blog
            </Link>
          </div>
        </div>
      </nav>
    )}
  />
)

export default NavBar
